import { useTranslation } from "react-i18next";
import Loading from "../../Loader/Loading";
import QvipleLoader from "../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import { useSelector } from "react-redux";
import { useEffect, useState } from "react";
import { useAllDepartment } from "../../../Department/Institute/Components/DepartmentApi/depart-api";
import { useFeedbackDepartmentAnalyticExcelExport } from "../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import style from "../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { pdfCdnUrl } from "../../Services/UrlConfig/BaseUrl";
import Modal from "../../Ui/Modal/Modal";
import SelectDepartmentCard from "../../../Profile/Institute/Pages/Site/SiteDepartment/SelectDepartmentCard";
const FeedbackSelectDepartment = ({ onClose, ifid, instituteId, isPdf }) => {
  const { t } = useTranslation();
  const ids = useSelector((state) => state.idChange.id);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [disabled, setDisabled] = useState(false);
  const { allDepartment, allDepartmentRefetch } = useAllDepartment({
    data: {
      id: instituteId ? instituteId : ids,
      which_type: "Normal",
    },
    skip: !instituteId ? !instituteId : !ids,
  });

  const [feedbackDepartmentAnalyticExcelExport] =
    useFeedbackDepartmentAnalyticExcelExport();
  useEffect(() => {
    if (allDepartment?.institute?.depart) {
      setShowingDataLoading(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [allDepartment?.institute?.depart]);

  useEffect(() => {
    if (ids) {
      setShowingDataLoading(true);
      allDepartmentRefetch();
    }
  }, [ids, allDepartmentRefetch]);

  const onExport = (did) => {
    if (did && ifid) {
      setDisabled((pre) => !pre);
      if (isPdf) {
        feedbackDepartmentAnalyticExcelExport({
          did: did,
          ifid: ifid,
          bodyParameter: {
            is_pdf: true,
          },
        })
          .then((res) => {
            if (res?.data?.excel_key) {
              setTimeout(() => {
                const newWindow = window.open(
                  `${pdfCdnUrl}/${res?.data?.excel_key}`,
                  "_blank",
                  "noopener,noreferrer"
                );
                if (newWindow) newWindow.opener = null;
                setDisabled((pre) => !pre);
                onClose();
              }, 2000);
            } else {
              setDisabled((pre) => !pre);
            }
          })
          .catch({});
      } else {
        feedbackDepartmentAnalyticExcelExport({
          did: did,
          ifid: ifid,
          bodyParameter: {
            is_pdf: false,
          },
        })
          .then((res) => {
            if (res?.data?.excel_key) {
              setDisabled((pre) => !pre);
              setTimeout(() => {
                const newWindow = window.open(
                  `${pdfCdnUrl}/${res?.data?.excel_key}`,
                  "_blank",
                  "noopener,noreferrer"
                );
                if (newWindow) newWindow.opener = null;
                setDisabled((pre) => !pre);
                onClose();
              }, 2000);
            } else {
              setDisabled((pre) => !pre);
            }
          })
          .catch({});
      }
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("select_department")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            {allDepartment?.institute?.depart?.map((depart) => (
              <div
                style={{
                  width: "100%",
                }}
                onClick={() => onExport(depart?._id)}
              >
                <SelectDepartmentCard
                  depart={depart}
                  key={depart._id}
                  isExcel
                />
              </div>
            ))}
            {showingDataLoading && <Loading />}
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default FeedbackSelectDepartment;
